@import "src/styles/variables";

.custom-input{
  transition: 0.2s;
  margin-top:7.5%;

  border: none;
  border-radius: 5px;
  outline: 2px solid #BEBEBE;
  width: 100%;
  height: 10%;
  font-size: 1.5rem;
  color: black;
  padding: 10px;
}
.custom-input:focus{
  outline-color: rgba(0,0,0,0.8);
}